
@font-face {  
  font-family: 'Raleway-Black';   
  src: url('./font/Raleway-Black.ttf');   
  font-weight: normal;  
  font-style: normal;  

}
body {
    padding:0;
    margin:0;
    font-family: 'Roboto', sans-serif;
}

 



/* login part */

.login_bg{
  background :-webkit-gradient(linear,left top, left bottom,from(#D03F8E),to(#0F4CB4));
  background :-webkit-linear-gradient(#D03F8E,#0F4CB4);
  background :-o-linear-gradient(#D03F8E,#0F4CB4);
  background :linear-gradient(#D03F8E,#0F4CB4);;
  height: 100vh;
  padding-top:8%;
}
 
.login_panel h1{
  font-size: 30px;
  font-family: Raleway-Black;
}



.login_panel{
  background: #fff;
  height: 500px;
  width:360px;
  margin:auto;
  padding:3rem;  
  text-align: center; 
  
}
.login_panel input {
  display:block;
  height: 45px;
  margin:25px auto;
  width:100%;
  background: #f2f2f2;
  border:0;
}
.login_panel button{
  display:block;
  height: 45px;
  margin:20px auto;
  background: #D03F8E;
  width:100%;
  color:white;
}
.login_panel button:hover{
  background: #D03F8E;
  cursor: pointer;
}


.home_login-message{
  opacity: 0.6;
  text-align: center;
}
.login_panel a{
     text-decoration: none;
     color:#4CAF50;
}





/* mainearea */



.maineare_bg{
  background: #F7F7F8;
  padding: 1rem;
  height: 100vh;
  padding-top:50px;
   
}
/*====================
        HEADER  table
======================*/

nav{
      
    background: #986CDB; 
}
nav button{
  color:white;
  font-weight: 500;
  border:0;
  background: #986CDB; 
  padding:0.5rem;  
}
nav button:hover{
  background: #998CDB;
}
.mainarea_stripped{
  background: #fff;
  margin: 1rem;
}
 .mainarea_plain{
  background: #F7F7F8;
  margin: 1rem;
 } 
 .mainarea_table{
   padding:1rem;
 }
 


/*====================
        Profile
======================*/

.profile_bg{
    background: #F7F7F8;
    height: 100vh;
  }
  
  
  .profile_container{
    
    display:-ms-flexbox;
    
    display:flex;  
    -ms-flex-align:start;  
        align-items:flex-start;
   
  
    
    
  }
  .profile_form{
    -ms-flex:2 1;
        flex:2 1;
    position: relative;
    background: #FFFFFF;
    margin :2rem;
    padding:0.5rem;
    padding-bottom: 50px;
   
  }
  .profile_form>div{
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 1rem;
  }
  
  
  
  .profile_finput{
    -ms-flex:1 1;
        flex:1 1;
    -ms-flex-positive:1;
        flex-grow:1;
    text-align: left;
  
    padding:10px;
    color: #555;
    font-size: 0.8rem;
  }
  
   
  .profile_form button{
    position: absolute;
    font-size: 1.2rem;
    color:white;
    bottom: 8px;
    right: 10px;
    background: #1DC7EA;
    width:150px;
    height: 39px;
    -webkit-box-shadow: none;
            box-shadow: none;
    border-radius: 5%;
  }
  .profile_finput input{
    display: block;
     width:100%;
     height: 30px;
     border:#ccc solid 1px;
     border-radius: 5%;
  }
  .profile_finput input:focus{
    background: #eee;
  }
  .profile_form button:hover{
    background: #5DCBEE;
  
  }
   
  
  /* css for image in Profile */
  .profile_img{
    -ms-flex:1 1;
        flex:1 1;
     text-align: center; 
    background:#FFFFFF;
   
    margin :2rem;
    padding:1rem;
  }
  .profile_img  img{
    border-radius: 50%;
    margin-top: 50px;
  }
  
  .profile_img p{
    width:200px;
    margin:auto;
    margin-bottom: 100px;
  }
  
  
  @media screen and (max-width:768px){
   .profile_container{
     display:block;
   }
  .profile_form>div{
  
    display:block;
  }   
  }


  


